import './intro.scss'
import React, { useEffect, useRef } from "react";
import { useInViewport } from 'react-in-viewport';
// import fixedBackground from './../../api/fixedBackgound';



function Intro() {
    const introRef = useRef();
    const { inViewport } = useInViewport(introRef);

    const fixedBackground = () => {
        const scrolledY = window.scrollY;
        introRef.current.style.backgroundPosition = 'center ' + ((scrolledY) + 'px');
    };


    useEffect(() => {
        window.addEventListener('scroll', fixedBackground);
        return () => {
            window.removeEventListener('scroll', fixedBackground)
        }
    }, [])

    return (
        <section ref={introRef} className="section-intro" id="home"
        >
            <div className={inViewport ? 'section-intro-wrap animate__animated animate__bounceInLeft animate__delay-0.5s' : 'section-intro-wrap-none'}>
                <h1 className="section-intro-title"> &laquo;&raquo;</h1>
                <p className="section-intro-subtitle">  </p>
                <p className="section-intro-subtitle"> </p>
            </div>
        </section>
    )
}

export default Intro;